.section-footer1 {
  background-color: $blue-siroco;
  padding-top: 5%;
  padding-bottom: 2%;
  position: relative;
  h5 {
    color: $white;
    font-family: "Roboto";
    font-weight: bold;
  }
  p {
    color: $white;
    font-family: "Roboto";
    font-weight: 300;
  }
  ul {
    padding-left: 0;
  }
  li {
    list-style: none;
    margin-bottom: .3rem;
    .items-menu {
      color: $white;
      font-family: "Roboto";
      font-weight: 300;
      text-decoration: none;
      &:hover {
        color: $yellow;
      }
    }
  }
  small {
    color: $white;
  }
  .dropdown-toggle::after {
    border-top: .3em solid $white !important;
    position: relative;
    left: 30px;
  }
  .dropdown-menu {
    min-width: 146px !important;
    padding: 0 !important;
    border-radius: 1.25rem !important;
    border: none;
    margin-bottom: -3px !important;
    li {
      margin-bottom: 0 !important;
    }
  }
  .dropdown-item {
    color: $blue-claro;
    background-color: $white !important;
    &:hover {
      color: $white;
      background-color: $blue-claro !important;
    }
  }
  .btn-idiomas {
    border-radius: 1.2em;
    background-color: #3FA9F7;
    text-align: left;
    max-width: 200px;
    .arrow-down {
      width: 0.8rem;
    }
    &.show {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }
  .logo-footer {
    width: 80%;
    @media (max-width: 767.98px) {
      width: 50%;
      max-width: 400px;
    }
  }
}
.section-footer2 {
  background-color: $blue-siroco2;
  padding: .5rem;
  p {
    color: $white;
    font-weight: 300;
  }
}
.whatsapp {
  position:fixed;
  width:60px;
  height:60px;
  bottom:40px;
  right:40px;
  background-image: url("../images/bg-whatsapp.png");
  background-size: cover;
  color:$white;
  border-radius:50px;
  text-align:center;
  font-size:30px;
  z-index:100;
  transition: all 0.3s;
  &:hover {
    transform: scale(0.8);
  }
}
.whatsapp-icon {
  width: 60%;
  margin-top: 10px;
}