.section-welcome {
  background-size: cover;
  background-position: center;
  .placa-blue {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba($color: #29ABE2, $alpha: 0.5);
    padding-bottom: 3rem;
    padding-top: calc(135px + 10rem);
  }
  .title-welcome {
    font-family: "Montserrat";
    font-size: 3rem;
    text-align: center;
    margin-bottom: 0;
    line-height: 1;
    @media (max-width: 424.98px) {
      font-size: 2rem;
    }
    span.yellow {
      color: $yellow;
    }
    span.white {
      color: $white;
    }
    span.text-800 {
      font-weight: 800;
    }
    span.text-400 {
      font-weight: 400;
    }
    span.text-200 {
      font-weight: 200;
    }
  }
  .subtitulo-welcome {
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 1.5rem;
    color: $white;
    text-align: center;
    margin-bottom: 0;
    @media (max-width: 424.98px) {
      font-size: 1rem;
    }
  }
  .radicalis-welcome {
    font-family: "radicalis";
    font-weight: 500;
    font-size: 3rem;
    text-align: center;
    margin-bottom: 0;
    color: $yellow;
    transform: translate(0,-20%) rotate(-3deg);
    letter-spacing: 2px;
    @media (max-width: 424.98px) {
      font-size: 2rem;
    }
  }
}

#welcome-index {
  .placa-blue {
    background-color: rgba($color: #29ABE2, $alpha: 0.3);
    padding-top: calc(135px + 5rem);
  }
  .title-welcome {
    font-size: 5rem;
    @media (max-width: 424.98px) {
      font-size: 2rem;
    }
  }
  .radicalis-welcome {
    font-size: 4rem;
    transform: translate(0,-25%) rotate(-3deg);
    letter-spacing: 2px;
    margin-bottom: 2rem;
    @media (max-width: 424.98px) {
      font-size: 2rem;
    }
  }
}